* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.list-item {
  margin-bottom: 5px;
}

.list-img {
  margin-right: 10px;
}

.info-name {
  display: inline;
  font-size: 50px;
}

.value {
  font-weight: 700;
  font-size: 20px;
  margin-right: 10px;
}

.desc {
  font-size: 25px;
}
